import React from 'react';
import { StyleSheet, TouchableWithoutFeedback, View } from 'react-native';
import Slider from 'react-native-slider';
import {Logger} from "../../utils/Utils";

export default class TimeSlider extends React.Component {
  constructor(props) {
    super(props);

    this.state = props.skinState;
    this.state.dragging = false;

    this.uzPlayer = props.player;
  }

  render() {
    return <Slider
      value={ this.state.playbackStatus.durationMillis ? (this.state.playbackStatus.positionMillis * 100 / this.state.playbackStatus.durationMillis) : 0 }
      step = { 1 }
      minimumValue={ 0 }
      maximumValue={ 100 }
      minimumTrackTintColor="#FFFFFF"
      thumbTintColor="#FFFFFF"
      thumbStyle={customStyles.thumb}
      // trackStyle={customStyles.track}
      style={styles.container}
      onSlidingComplete={(value) => {
        const positionMilis = value * this.state.playbackStatus.durationMillis / 100;
        const pos = parseInt(positionMilis / 1000, 10);
        this.uzPlayer.seek(pos);
        this.setState({ dragging: false, sliderValue: value });
      }}
      onValueChange={(value) => {
        this.setState({ dragging: true, sliderValue: value });
      }}
    />
  }
}

const styles = StyleSheet.create({
  container: {
    zIndex: 1,
    width: '100%',
    height: 20,
  }
});
var customStyles = StyleSheet.create({
    track: {
        height: 1,
        backgroundColor: '#303030',
    },
    thumb: {
        width: 8,
        height: 8,
        backgroundColor: 'rgba(255, 255, 255, 1)',
        borderColor: 'rgba(255, 255, 255, 1)',
        borderWidth: 8,
        borderRadius: 15,
    }
});
